");

// Logotype
.logotype
{
  @include text-hide();
  display: block;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: $logotype;
  &.logotype-black
  {
    background-image: $logotype-black;
  }
  @include hover-focus-active
  {
    text-decoration: none;
    outline: 0;
  }
  width: 120px;
  height: 60px;
  &.logotype-md
  {
    width: 180px;
    height: 90px;
  }
  &.logotype-lg
  {
    width: 240px;
    height: 120px;
  }
}